  padding: 0;
}

@media (min-width: 40em) {
  .header {
    margin: 0 auto;
    max-inline-size: 700px;
  }
  .header picture .desktop-overlay {
    visibility: visible;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    width: 100%;
    background-size: cover;
  }
  .header picture .mobile-overlay {
    visibility: hidden;
  }
  .header__cta {
    flex-flow: row wrap;
    gap: 1.5625rem;
  }

  .about__section {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(21.875rem, 1fr));
  }
  .about__section .about__box:nth-of-type(3) > p {
    margin-bottom: 6rem;
  }

  .download {
    margin: 0 auto;
    max-inline-size: 700px;
  }

  .services__subtitle {
    margin: 0 auto;
    max-inline-size: 700px;
  }
  .services__container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(25rem, 1fr));
    gap: 5.5rem;
    margin-inline: 1.5rem;
  }
}
@media (min-width: 40em) and (min-width: 71.875em) {
  .services__container {
    text-align: left;
    margin: 0;
  }
}
@media (min-width: 40em) {
  .services__container figure img {
    transform: translateX(-10%);
    max-width: 220%;
  }
}
@media (min-width: 40em) and (min-width: 71.875em) {
  .services__aside {
    text-align: left;
  }
}
@media (min-width: 40em) {
  .design__subtitle {
    margin: 0 auto;
    max-inline-size: 700px;
  }
  .design__hero {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
@media (min-width: 40em) {
